.table-components tbody td:first-child {
  color: #dc3545;
  font-size: 12px;
  font-family: "Roboto Mono", monospace;
} */
.dataTables_filter{
  display: none;
}
.dataTable_info>{
  font-size: 1px;
  font-family: -apple-system, BlinkMacSystemFont, "Inter UI", Roboto, sans-serif;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  padding: 5px 10px;
  border-bottom: 0;
  border-color: rgba(72, 94, 144, 0.16);
  background-color: #f5f6fa;
  white-space: nowrap;

}
.dataTable_paginate>a .current{
  font-size: 1px;
  font-family: -apple-system, BlinkMacSystemFont, "Inter UI", Roboto, sans-serif;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  padding: 5px 10px;
  border-bottom: 0;
  border-color: rgba(72, 94, 144, 0.16);
  background-color: #f5f6fa;
  white-space: nowrap;

}
.custom-checkbox
  .custom-control-input:disabled:checked
  ~ .custom-control-label::before,
.custom-radio
  .custom-control-input:disabled:checked
  ~ .custom-control-label::before,
.custom-switch
  .custom-control-input:disabled:checked
  ~ .custom-control-label::before {
  border-color: rgba(1, 104, 250, 0.2);
}

.custom-checkbox .custom-control-label::before,
.custom-checkbox .custom-control-label::after,
.custom-radio .custom-control-label::before,
.custom-radio .custom-control-label::after,
.custom-switch .custom-control-label::before,
.custom-switch .custom-control-label::after {
  top: 44%;
  transform: translateY(-50%);
}

.custom-checkbox .custom-control-label:empty,
.custom-radio .custom-control-label:empty,
.custom-switch .custom-control-label:empty {
  height: 20px;
}
.custom-checkbox .custom-control-label:empty::before,
.custom-checkbox .custom-control-label:empty::after,
.custom-radio .custom-control-label:empty::before,
.custom-radio .custom-control-label:empty::after,
.custom-switch .custom-control-label:empty::before,
.custom-switch .custom-control-label:empty::after {
  left: -16px;
}

.autocomplete {
  /*the container must be positioned relative:*/
  position: relative;
  display: inline-block;
}

.autocomplete-items {
  position: absolute;
  border: 1px solid #d4d4d4;
  border-bottom: none;
  border-top: none;
  z-index: 99;
  /*position the autocomplete items to be the same width as the container:*/
  top: 100%;
  left: 0;
  right: 0;
}
.autocomplete-items div {
  padding: 10px;
  cursor: pointer;
  background-color: #fff;
  border-bottom: 1px solid #d4d4d4;
}
.autocomplete-items div:hover {
  /*when hovering an item:*/
  background-color: #e9e9e9;
}
.autocomplete-active {
  /*when navigating through the items using the arrow keys:*/
  background-color: DodgerBlue !important;
  color: #ffffff;
}

#addrow:hover {
  cursor: pointer;
}
#delete:hover {
  cursor: pointer;
}
#drpmoreadd:hover{
  color: #fff;
}